#form_notify_param {
  .form-notify-box {
    x-height: 700px;
    overflow: scroll;
  }

  .form-notify-box__html-container > div {
    width: 100%;
  }

  .form-notify-params-toggle {
    & > h3 {
      .toggle-indicator {
        transform: rotate(180deg);
      }
    }

    & > div {
      display: none;
    }

    &.active {
      .toggle-indicator {
        top: 3px;
        right: 7px;
        transform: rotate(0deg);
      }

      & > div {
        display: block;
      }
    }
  }
}

#js-form_notify_trigger_rule-repeated-blocks .form-notify-box__repeated {
  position: relative;

  &:after {
    content: '- AND -';
    display: block;
    text-align: center;
    width: 100%;
  }

  &:last-child:after {
    content: '';
  }
}

